  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}
.duration-1000{
  transition-duration: 1000ms;
}
.duration-200{
  transition-duration: 200ms;
}
.duration-300{
  transition-duration: 300ms;
}
.duration-500{
  transition-duration: 500ms;
}
.duration-700{
  transition-duration: 700ms;
}
.ease-out{
  transition-timing-function: cubic-bezier(0, 0, 0.2, 1);
}

:root {
  --background: #ededef;
}

body {
  background-color: var(--background);
  font-family:
    "IBM Plex Sans KR",
    "Pretendard",
    "Blinker" "Geist";
}

h2 {
  font-size: 56px;
  font-weight: 500;
}
/* globals.css */
@keyframes fadein {
  to {
    opacity: 1;
  }
}

@keyframes fadein-up {
  from {
    opacity: 0;
    transform: translateY(40px); /* 아래에서 시작 */
  }
  to {
    opacity: 1;
    transform: translateY(0); /* 제자리로 */
  }
}

@keyframes center-to-up {
  0% {
    position: fixed;
    left: 50%;
    top: 46%;
    transform: translate(-50%, -50%);
  }
  100% {
    position: fixed;
    left: 50%;
    top: 46%;
    transform: translate(-50%, calc(-46% - 190px));
  }
}

@keyframes fadein-up-under {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(-25%);
    animation-timing-function: cubic-bezier(0.8, 0, 1, 1);
  }
  50% {
    transform: none;
    animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  }
}

@keyframes fadein-down {
  0% {
    opacity: 0;
    transform: translateY(-60px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.fadein {
  opacity: 0;
  animation: fadein 2s 0s forwards;
}

.fadein-fast {
  opacity: 0;
  animation: fadein 0.5s 0s forwards;
}

.fadein-up {
  opacity: 0;
  animation: fadein-up 0.8s ease-in-out 1s forwards;
}

.fadein-down {
  animation: fadein-down 1s 4s both;
}

.fadein-down-delay {
  animation: fadein-down 1s 4.5s both;
}

.fadein-delay {
  opacity: 0;
  animation: fadein 2s 3s forwards;
}

.fadein-bg {
  opacity: 0;
  animation: fadein 2s 3s forwards;
}

.fadein-profile {
  opacity: 0;
  animation: fadein 1s 3s forwards;
}

.center-to-up {
  animation: center-to-up 1.5s ease-in-out forwards;
  animation-delay: 3s;
}

.fadein-bounce {
  opacity: 0;
  animation:
    fadein-up 1s 4.5s forwards,
    bounce 0.7s 0s infinite;
}

.text-sm-res {
  font-size: clamp();
}

.modal {
  width: 80%;
  max-width: 80vw;
  margin-top: 20px;
  border-radius: 5px;
  border: none;
}

.modal::backdrop {
  background: rgba(0, 0, 0, 0.7);
}

.hover\:bg-black:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(0 0 0 / var(--tw-bg-opacity));
}

.hover\:bg-white:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
}

.hover\:text-\[\#747474\]:hover{
  --tw-text-opacity: 1;
  color: rgb(116 116 116 / var(--tw-text-opacity));
}

.hover\:text-black:hover{
  --tw-text-opacity: 1;
  color: rgb(0 0 0 / var(--tw-text-opacity));
}

.hover\:text-white:hover{
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
}

.hover\:shadow-lg:hover{
  --tw-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --tw-shadow-colored: 0 10px 15px -3px var(--tw-shadow-color), 0 4px 6px -4px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.hover\:ring-4:hover{
  --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
  --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(4px + var(--tw-ring-offset-width)) var(--tw-ring-color);
  box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
}

.hover\:ring-gray-200:hover{
  --tw-ring-opacity: 1;
  --tw-ring-color: rgb(229 231 235 / var(--tw-ring-opacity));
}

@media (min-width: 768px){

  .md\:text-3xl{
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  .md\:text-8xl{
    font-size: 6rem;
    line-height: 1;
  }
}

@media (min-width: 1024px){

  .lg\:text-5xl{
    font-size: 3rem;
    line-height: 1;
  }

  .lg\:text-9xl{
    font-size: 8rem;
    line-height: 1;
  }
}
